$breakpoints: (
	"phone": 400px,
	"phone-wide": 480px,
	"phablet": 560px,
	"tablet-small": 640px,
	"tablet": 768px,
	"tablet-wide": 1024px,
	"desktop": 1248px,
	"desktop-wide": 1440px
);

@mixin mq($width, $type: min) {
	@if map_has_key($breakpoints, $width) {

		$width: map_get($breakpoints, $width);

		@if $type == max {
			$width: $width - 1px;
		}

		$parent: &;

		@media only screen and (#{$type}-width: $width) {
			@at-root #{$parent} {
				@content;
			}
		}
	}
}

/* Mobile First */
@media (min-width: 400px) {
	.navigation {
	   width: 4em;
	   left: 0;
	}
 }
 
 @media (min-width: 600px) {
	
	.navigation {
	   width: 15em;
	}
 }
 
